/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.focused-content {
  max-width: 80rem;
}

.align-right {
  text-align: right;
}

.btn-orange {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  transition: all 0.5s ease-in-out;
  border: none;
}
.btn-orange:hover {
  background-color: #af6907;
  cursor: pointer;
}

.btn-orange-inline {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  transition: all 0.5s ease-in-out;
  border: none;
}
.btn-orange-inline:hover {
  background-color: #af6907;
  cursor: pointer;
}

.btn-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  transition: all 0.5s ease-in-out;
  border: none;
}
.btn-green:hover {
  background-color: #507e00;
  cursor: pointer;
}

.btn-green-inline {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  transition: all 0.5s ease-in-out;
  border: none;
}
.btn-green-inline:hover {
  background-color: #507e00;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .adds {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .adds .icons-characteristics {
  filter: invert(100%);
}
.dark-mode .icons-characteristics li p {
  filter: invert(100%);
}
.dark-mode .formulario legend {
  color: #FFFFFF;
}
.dark-mode .formulario label {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #1a1a1a;
  color: #e1e1e1;
}
.dark-mode .formulario p {
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .header-content {
  height: 70rem;
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

@media (min-width: 768px) {
  .icons-about-us {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right {
    align-items: flex-end;
  }
}
.right .dark-mode-btn {
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 3rem;
  filter: invert(100%);
}
.right .dark-mode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .right .dark-mode-btn {
    margin-top: 0;
  }
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navigation-bar {
  height: 0;
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 768px) {
  .navigation-bar {
    height: auto;
    opacity: 1;
    visibility: visible;
    display: flex;
    flex-direction: row;
  }
}
.navigation-bar a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: inline-block;
  text-align: center;
  width: 100%;
  transition: color 0.5s ease-in-out;
}
.navigation-bar a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navigation-bar a {
    margin-right: 2rem;
    font-size: 1.8rem;
    border: none;
  }
  .navigation-bar a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  margin-top: 2rem;
  margin-bottom: 1rem;
  width: 4rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
  transition: visibility 0.5s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}

.no-webp .img-contact {
  background-image: url(../img/encuentra.jpg);
}

.webp .img-contact {
  background-image: url(../img/encuentra.webp);
}

.img-contact {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img-contact h2, .img-contact p {
  color: #FFFFFF;
}
.img-contact h2 {
  font-size: 4rem;
  font-weight: 900;
}
.img-contact p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .container-adds {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.adds {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
}

.content-adds {
  padding: 1rem;
}
.content-adds h3, .content-adds p {
  margin: 0;
}
.content-adds p {
  font-size: 2rem;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-characteristics {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons-characteristics li {
  flex: 1;
  display: flex;
}
.icons-characteristics li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-characteristics li p {
  font-weight: 700;
}

.resumen-propiedad {
  text-align: justify;
}

.testimonial {
  border-radius: 2rem;
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  position: absolute;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  height: 8rem;
  width: 6rem;
  display: block;
  top: 0;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .about-us {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.text-about {
  width: 100%;
}
.text-about p:first-of-type {
  margin: 2rem;
}
@media (min-width: 768px) {
  .text-about p:first-of-type {
    margin: 0rem;
  }
}
.text-about span {
  font-weight: 900;
  margin: 0;
  font-size: 2.2rem;
}

@media (min-width: 768px) {
  .bottom-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.into-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .into-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.into-blog:last-of-type {
  margin-bottom: 0;
}

.into-text a {
  text-decoration: none;
  color: #000000;
}
.into-text h4 {
  margin: 0;
  line-height: 1.4;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .into-text h4 {
    margin-top: 0;
  }
}
.into-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  margin-top: 1rem;
  background-color: #71B100;
}

.meta-information span {
  color: #E08709;
  font-weight: 700;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: none;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin-bottom: 0;
}
.formulario textarea {
  height: 20rem;
  max-height: 20rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
.footer .footer-container .navigation-bar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 768px) {
  .footer .footer-container .navigation-bar {
    width: 30%;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}